<template>
    <div>
        <div class="box">
            <Head>
                <router-link to="/List" slot="left">
                    <img src="../../images/110.png" alt="">
                </router-link>
                <span slot="center">账号注册</span>
            </Head>
            <div class="con">
                <div class="span">
                    <input class="user" type="text" placeholder="账号" v-model="username" />
                    <input class="pass" v-show="!value2" type="password" placeholder="密码" v-model="password" />
                    <input class="pass" v-show="value2" type="text" placeholder="密码" v-model="password" />
                    <input class="yan" type="text" placeholder="验证码" v-model="captcha_code">
                    <span><img :src="code" alt=""></span>
                    <span>看不清</span>
                    <span @click="chang">换一张</span>
                    <el-switch
                        id="an"
                        class="an"
                        v-model="value2"
                        active-color="#13ce66"
                        inactive-color="#ff4949">
                    </el-switch>
                </div>
                <span class="wenxin">温馨提示：未注册过的账号，登陆时将自动注册</span>
                <span class="tishi">注册过的用户可凭账号账号密码登陆</span>
                <button @click="sub">注册</button>
                <p>重置密码？</p>
            </div>
            <div class="alert" v-show="isNo==true">
                <span>!</span>
                <span>{{val}}</span>
                <button @click="add">确定</button>
            </div>
        </div>
    </div>
</template>

<script>
import Head from  '../Head/Header.vue'
import axios from 'axios'
axios.defaults.withCredentials=true
export default {
    components:{
        Head
    },
    data(){
        return{
            username:'',
            password:'',
            isWorks:false,
            num:'',
            value1: true,
            value2: false,
            captcha_code:'',
            isShow:false,
            popupVisible:'请输入验证码',
            isNo:false,
            val:'',
            code:''
        }
    },
    methods:{
        sub(){
            let a = '';
            for(var i = 0 ; i < 6 ; i++ ){
                a+=parseInt(Math.random()*10)
            }
            localStorage.setItem("a",a)
            this.$router.push({name:"Login"})
            if(localStorage.getItem(this.username)===null){
                localStorage.setItem(this.username,this.password)
                this.isWorks = true
            }
            if(this.sum.length==0){
                this.isShow = true;
            }        
            if(this.username == "" ){
                this.isNo = true;
                this.val = "请输入用户名";
            }else if(this.password == "" ){
                this.isNo = true;
                this.val = "请输入密码";
            } else if(this.sum == "" ){
                this.isNo = true;
                this.val = "请输入验证码";
            }else if(this.code.status==0){
                this.isNo = true;
                this.val = res.data.message;
            }else if(this.code.status!=0){
                this.$router.push({name:"Login"})
            }
        },
        chang(){
            axios.post('https://elm.cangdu.org/v1/captchas',
            {})
            .then((res)=>{
                if(res.data.code!=''){
                    this.code=res.data.code
                }
            })
        },
        add(){
            this.isNo = false;
        }
    },
    mounted(){
        function  rem(){
		document.documentElement.style.fontSize=document.documentElement.clientWidth/7.5+"px";
	}
	//onresize  当屏幕的尺寸发生改变的时候会触发的事件
	rem();
  window.onresize=rem;
        this.chang();
    }
}
</script>

<style lang="less" scoped>
    .box{
        width: 100%;
        // height: 7.9rem;
        background: #F5F5F5;
        header{
            width: 100%;
            height: 1rem;
            background: #3792E5;
            position: relative;
            img{
                width: 0.4rem;
                position: absolute;
                top: 0.3rem;
                left: 0.2rem;
            }
            span{
                display: block;
                position: absolute;
                top: 0.25rem;
                color: white;
                font-size: 0.4rem;
                left: 2.8rem;
            }
        }
        .con{
            width: 100%;
            // height: 0.9rem;
            .span{
                width: 100%;
                height: 3.5rem;
                display: block;
                position: relative;
                input{
                    width: 100%;
                    height: 1.1rem;
                    text-indent: 0.2rem;
                    border-left: none;
                    border-right: none;
                    border-top: none;
                    font-size: 0.35rem;
                    color: #A9A9A9;
                }
                .user{
                   position: absolute;
                   top: 0.2rem;
                }
                .pass{
                    position: absolute;
                    top: 1.35rem;
                }
                .yan{
                    position: absolute;
                    top: 2.5rem;
                    margin-left: 0;
                }
                span{
                    display: block;
                    font-size: 0.35rem;
                    position: absolute;
                    top: 2.8rem;
                    right: 1.7rem;
                    // padding: 5rem;
                }
                span:nth-of-type(2){
                    display: block;
                    font-size: 0.3rem;
                    position: absolute;
                    top: 2.6rem;
                    right: 0.5rem;
                }
                span:nth-of-type(3){
                    display: block;
                    font-size: 0.3rem;
                    position: absolute;
                    top: 3.1rem;
                    right: 0.5rem;
                    color: #3792E5;
                }
                .an{
                    position: absolute;
                    top: 1.75rem;
                    right: 0.5rem;
                }
            }
            .wenxin{
                display: block;
                color: #FF0000;
                font-size: 0.25rem;
                margin: 0.4rem 0 0 0.2rem;
            }
            .tishi{
                display: block;
                color: #FF0000;
                font-size: 0.25rem;
                margin: 0.2rem 0 0 0.2rem;
            }
            button{
                width: 6.4rem;
                height: 0.7rem;
                border: none;
                margin-left: 0.5rem;
                background: #4CD964;
                color: white;
                font-size: 0.35rem;
            }
            p{
                color: #3B95E9;
                font-size: 0.3rem;
                float: right;
                padding-right: 0.4rem;
            }
        }
        .alert{
            width: 80%;
            height: 6rem;
            background: white;
            border: 1px solid black;
            font-size: 0.5rem;
            position: fixed;
            top: 3rem;
            left: 0.7rem;
            span{
                display: block;
            }
            span:nth-of-type(1){
                width: 3rem;
                height: 3.1rem,;
                border-radius: 50%;
                margin-left: 1.5rem;
                margin-top: 0.3rem;
                border: 1px solid rgb(136, 138, 32);
                font-size: 2rem;
                text-align: center;
                line-height: 3.1rem;
                color: rgb(205, 207, 54);
            }
            span:nth-of-type(2){
                text-align: center;
                margin-top: 0.5rem;
            }
            button{
                width: 100%;
                height: 0.7rem;
                border: none;
                background: #4CD964;
                font-size: 0.4rem;
                color: white;
                margin-top: 0.7rem;
            }
        }
    }
</style>


